import { MacroSearchItem } from "@rp/react/tanstack-api/cqs-query-context"

const generateMacroIds = (macroFilter: Pick<MacroSearchItem, "type" | "value">) => {
  const ids: Array<string> = []
  const baseId = macroFilter.type

  for (const group of macroFilter.value) {
    const groupId = group.type

    for (const key of group.value) {
      ids.push(`${baseId}-${groupId}-${key}`)
    }
  }

  return ids
}

export { generateMacroIds }
